import "core-js/modules/es.array.push.js";
import { ref } from '@vue/reactivity';
import { useRouter } from 'vue-router';
export default {
  setup() {
    let goback = () => {
      router.push({
        path: "/settlement"
      });
    };
    //获得传来的数据
    // 地址数据
    let router = useRouter();
    let lists = JSON.parse(window.localStorage.getItem("address"));

    //判断是否有值
    let haveshow = ref(true);
    // 如果没有这个东西或者长度为0
    if (!lists || lists.length == 0) {
      haveshow.value = true;
      //否则现实地址数据
    } else {
      haveshow.value = false;
    }

    //新增地址跳转
    let addlocal = () => {
      router.push({
        path: "/addaddress",
        query: {
          "num": 1
        }
      });
      // router.push({path:"/addaddress",params:"1"})
    };

    //修改地址跳转
    let edit = id => {
      console.log("1222222");
      router.push({
        path: "/addaddress",
        query: {
          "num": 2,
          "id": id
        }
      });
    };

    //选择地址
    let choose = id => {
      console.log(id);
      router.push({
        path: "/settlement"
      });
      window.localStorage.setItem("num", JSON.stringify(id));
    };
    return {
      lists,
      addlocal,
      edit,
      goback,
      choose,
      haveshow
    };
  }
};